@import "/static/scss/vars";

.plover-icon-settings-panel__icon-picker {
  border: 1px solid $wp-border-color;
}

.plover-icon-settings-panel__popover-content {
  min-width: 270px;
  padding: 16px;

  .plover-icon-settings-panel__no-result {
    text-align: center;
  }
}

.plover-icon-settings-panel__icon-list {
  //grid-column-gap: 12px;
  //grid-row-gap: 12px;
  //display: grid;
  //grid-template-columns: repeat(5, 1fr);
  //justify-items: center;
  //max-height: 280px;
  //overflow-x: hidden;
  //overflow-y: auto;
  margin: 0 -16px;
  padding: 0 16px;

  .plover-icon-settings-panel__icon-preview {
    padding: 8px;
    border: 1px solid $wp-border-color;
    background-color: transparent;
    height: auto;
    line-height: 0;
    cursor: pointer;
    color: currentColor;
    fill: currentColor;
    border-radius: 2px;
    transition: all ease .2s;

    &:hover, &.active {
      color: #ffffff;
      border-color: $wp-primary;
      background-color: $wp-primary;
    }

    svg {
      width: 20px;
      height: 20px;
    }
  }
}

.wp-block-button[class*=has-icon__] {
  .wp-block-button__link {
    &::after {
      content: "";
    }

    &::after {
      display: inline-block;
      background-color: currentColor;
      -webkit-mask-size: contain;
      mask-size: contain;
      -webkit-mask-position: center;
      mask-position: center;
      -webkit-mask-repeat: no-repeat;
      mask-repeat: no-repeat;
      height: var(--wp--custom--icon--size, 1em);
      width: var(--wp--custom--icon--size, 1em);
      mask-image: var(--wp--custom--icon--url);
      -webkit-mask-image: var(--wp--custom--icon--url);
    }
  }

  &.has-icon-position__left {
    .wp-block-button__link::after {
      order: -1;
    }
  }
}

.icon-library-not-available {
  svg,
  .wp-block-button__link::after {
    opacity: .25;
  }
}
